import ImageModal from "@/components/content/imageModal/ImageModal";
export default {
    name: "TeamList",
    data() {
        return {
            baseUrl: this.$baseUrl,
            companyMsg: this.$store.state.companyMsg,
            isShowModal:false,
            codeSrc: ''
        }
    },
    components:{
        ImageModal
    },
    props: {
        teamData: {
            default() {
                return []
            },
            type: Array
        }
    },
    methods:{
        showModal(wx){
            this.codeSrc = wx.src
            this.isShowModal = true
        },
        closeModal(){
            this.isShowModal = false
        }
    }
}
